
body {
  padding: 0;
  margin: 0;
}
#app {
  font-family: Arial, 'Avenir', Helvetica, Verdana, Helvetica, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  max-width: 100%;
  font-size: 0.9rem;
}
.block {
  height: auto;
  min-height: 100vh;
}
.block.intro {
  text-align: center;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  color: #fff; /* ff3.6+ */ /* safari4+,chrome */
/* safari5.1+,chrome10+ */ /* opera 11.10+ */ /* ie10+ */
  background: radial-gradient(ellipse at center, #76a8c4 0%, #008080 100%);
/* w3c */
}
.block.intro >div {
  -webkit-box-flex: 1;
      -ms-flex: auto;
          flex: auto;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.block.intro h1 {
  color: #ffc;
}
.block.docs {
  text-align: left;
  width: 800px;
  margin: auto;
}
h1 {
  font-family: Montserrat, sans-serif;
  font-size: 4rem;
  margin-bottom: 1rem;
}
h3 {
  font-family: Montserrat, sans-serif;
  font-size: 1.6rem;
  margin-bottom: 1rem;
}
.motto {
  margin-bottom: 2rem;
}
.docs {
  text-align: left;
  margin-top: 5rem;
}
.standard {
  margin: 2rem auto;
  font-family: sans-serif;
  font-size: 1.6rem;
}
.controls {
  margin-top: 0.5rem;
}
.controls input {
  padding: 0 0.2rem;
  line-height: 1.6rem;
}
.controls label {
  height: 2rem;
  line-height: 2rem;
  display: inline-block;
  vertical-align: text-bottom;
}
code {
  padding: 0.5rem;
  font-family: monospace;
  border: 1px solid #9c9;
  text-align: left;
  white-space: pre-wrap;
  display: block;
  max-width: 100%;
  width: auto;
  margin: auto;
}
